:root {
    --container-bg-color: #333;
    --left-bg-color: rgba(223, 39, 39, 0.7);
    --left-button-hover-color: rgba(161, 11, 11, 0.3);
    --right-bg-color: rgba(43, 43, 43, 0.8);
    --right-button-hover-color: rgba(92, 92, 92, 0.3);
    --hover-width: 90%;
    --other-width: 10%;
    --speed: 1000ms;
  }
  
  .body {
    padding:0;
    margin:0;
    font-family: 'Srisakdi', cursive;

    width: 100%;
    height: 100%;
    overflow-x: hidden;
  }
  
  .body h1 {
    font-size: 4rem;
    color: #fff;
    position: absolute;
    left: 50%;
    top: 20%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    white-space: nowrap;
  }
  
  .body .button {
    display: block;
    position: absolute;
    left: 50%;
    top: 40%;
    height: 4rem;
    padding-top: 1.3rem;
    width: 15rem;
    text-align: center;
    color: #fff;
    border: #fff solid 0.2rem;
    font-size: 1rem;
    font-weight: bold;
    text-transform: uppercase;
    text-decoration: none;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
  
  .body .split.left .button:hover {
    background-color: rgba(161, 11, 11, 0.3);
    background-color: var(--left-button-hover-color);
    border-color: rgba(161, 11, 11, 0.3);
    border-color: var(--left-button-hover-color);
  }
  
  .body .split.right .button:hover {
    background-color: rgba(92, 92, 92, 0.3);
    background-color: var(--right-button-hover-color);
    border-color: rgba(92, 92, 92, 0.3);
    border-color: var(--right-button-hover-color);
  }
  
  .body .container {
    position: relative;
    width: 100%;
    height: 100%;
    background: #333;
    background: var(--container-bg-color);
  }
  
  .body .split {
    position: absolute;
    width: 50%;
    height: 100%;
    overflow: hidden;
  }
  
  .body .split.left {
    left:0;
    background: url(/portfolio3/static/media/tulips.41944c5b.jpg) center center no-repeat;
    background-size: cover;
  }
  
  .body .split.left:before {
    position:absolute;
    content: "";
    width: 100%;
    height: 100%;
    background: rgba(223, 39, 39, 0.7);
    background: var(--left-bg-color);
  }
  
  .body .split.right {
    right:0;
    background: url(/portfolio3/static/media/dlanor-s-703975-unsplash.3b77c38b.jpg) center center no-repeat;
    background-size: cover;
  }
  
  .body .split.right:before {
    position:absolute;
    content: "";
    width: 100%;
    height: 100%;
    background: rgba(43, 43, 43, 0.8);
    background: var(--right-bg-color);
  }
  
  .body .split.left, .split.right, .split.right:before, .split.left:before {
    transition: 1000ms all ease-in-out;
    transition: var(--speed) all ease-in-out;
  }
  
  .body .hover-left .left {
    width: 90%;
    width: var(--hover-width);
  }
  
  .body .hover-left .right {
    width: 10%;
    width: var(--other-width);
  }
  
  .body .hover-left .right:before {
    z-index: 2;
  }
  
  
  .body .hover-right .right {
    width: 90%;
    width: var(--hover-width);
  }
  
  .body .hover-right .left {
    width: 10%;
    width: var(--other-width);
  }
  
  .body .hover-right .left:before {
    z-index: 2;
  }
  
  @media(max-width: 800px) {
    .body h1 {
      font-size: 2rem;
    }
  
    .body .button {
      width: 10rem;
    }
  }
  
  @media(max-height: 700px) {
    .body .button {
      top: 70%;
    
    }
  }
